
import PackService from "@/services/PackService";

export default {
  methods: {
    resetPackRegistry() {
      if (confirm("Are you sure?")) {
        PackService.resetRegistry()
        .then((response) => {
            alert(response.data.message);
          })
        .catch((error) => {
          alert(error.response.data.message);
        })
        .finally(() => {
          window.location.reload();
        });
      }
    },
  },
};
